<template>
  <v-container fluid>
    <v-row justify="center">
      <v-col class="mb-4" cols="10">
        <v-select
          :items="items"
          item-text="text"
          item-value="value"
          label="הגדרת אזור"
          v-model="color"
          @change="colorSelected"
        ></v-select>
      </v-col>
    </v-row>

    <v-row justify="center">
      <v-col class="mb-4" cols="10">
        <div>
          <v-text-field v-model="structure_size" label="גודל המבנה (מטרים רבועים)" required></v-text-field>
        </div>
      </v-col>
    </v-row>

    <v-row justify="center">
      <v-col class="mb-4" cols="10">
        <div>
          <v-text-field v-model="num_enternces" label="מספר כניסות" required></v-text-field>
        </div>
      </v-col>
    </v-row>

    <v-row justify="center" class="text-center">
      <v-col class="mb-4" cols="10">
        בחלל זה ניתן לקיים תפילה בנוכחות
        <h1 class="display-2 font-weight-bold mb-3">{{result}} מתפללים</h1>
        <hr>
        <h2 v-if="result">{{finalNumGroups}} קפסולות של {{finalCapsuleSize}} מתפללים</h2>
        <h3 v-if="addedOnTop && num_groups > 1">ועוד {{this.addedOnTop}} בקפסולה נפרדת</h3>

        <p class="subheading font-weight-regular">
          השימוש במחשבון על אחריות המשתמש בלבד
          <br>להוראות משרד הבריאות
          <a
            href="https://www.gov.il/he/departments/news/13092020_04"
            target="_blank"
          >הקליקו כאן</a>
        </p>
        <p class="subheading font-weight-regular">
          העדיפו תפילה באוויר הפתוח
          <br>עטו מסיכות ושמרו על ריחוק חברתי
        </p>
        <p class="subheading font-weight-bold">ונשמרתם מאוד לנפשותיכם</p>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import CONSTANTS from "../CONSTANTS";
export default {
  name: "SegerCalc",

  data: () => ({
    calc_params: {
      structure_size: 300,
      num_enternces: 2,
      color: CONSTANTS.COLORS.green
    },
    items: [
      { text: "ירוק", value: "green" },
      { text: "צהוב", value: "yellow" },
      { text: "כתום", value: "orange" },
      { text: "אדום", value: "red" }
    ],
    structure_size: 300,
    num_enternces: 2,
    structure_open: true,
    color: CONSTANTS.COLORS.green,
    num_groups: 1,
    capsule_size: CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE["green"],
    result: null,
    addedOnTop: 0
  }),

  mounted: function() {
    this.getResult();
  },
  watch: {
    color: function() {
      this.calc_params.color = this.color.toString();
      this.getResult();
    },
    structure_size: function() {
      this.calc_params.structure_size = +this.structure_size;
      this.getResult();
    },
    num_enternces: function() {
      this.calc_params.num_enternces = +this.num_enternces;
      this.getResult();
    }
  },
  computed: {
    HEBREW_COLORS: function() {
      return CONSTANTS.HEBREW_COLORS;
    },
    finalCapsuleSize() {
      if (this.num_groups === 1 && this.addedOnTop) {
        return this.addedOnTop;
      } else {
        return this.capsule_size;
      }
    },
    finalNumGroups() {
      if (this.addedOnTop && this.num_groups > 1) {
        return this.num_groups - 1;
      } else {
        return this.num_groups;
      }
    }
  },
  methods: {
    colorSelected(color) {
      this.capsule_size = CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[color];
    },

    getResult: function() {
      const maxPeopleBySqM = this.calc_params.structure_size / 4; //Max people - one for every 4 sqM

      const factor =
        this.calc_params.num_enternces * 3 +
        Math.max(0, this.calc_params.num_enternces - 3) * 2;

      const maxPeopleByCapsules =
        factor *
        CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color];

      const temp_result = Math.floor(
        Math.round(Math.min(maxPeopleBySqM, maxPeopleByCapsules))
      );

      console.log("temp_result", temp_result);
      this.result =
        temp_result -
        (temp_result %
          CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color]);

      this.num_groups = Math.floor(
        Math.max(
          1,
          this.result /
            CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color]
        )
      );

      // Do we have some room left?
      const roomLeft = this.calc_params.structure_size - this.result * 4;

      // How many people can be in that space?
      const peopleInRoomLeft = Math.min(
        CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color],
        Math.floor(roomLeft / 4)
      ); //4 sqM for each

      // If the number is lower or equal to the allowed capsule size - add it
      this.addedOnTop =
        this.num_groups < factor &&
        (peopleInRoomLeft <=
        CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color]
          ? peopleInRoomLeft
          : 0);

      if (this.addedOnTop) {
        this.result += this.addedOnTop;
        if (this.result > this.addedOnTop) {
          this.num_groups++;
        }
      }

      console.log(
        `maxPeopleBySqM: ${maxPeopleBySqM}`,
        factor,
        maxPeopleByCapsules,
        temp_result,
        this.result,
        this.num_groups,
        `roomLeft: ${roomLeft}`,
        `peopleInRoomLeft: ${peopleInRoomLeft}`
      );
    }
  }
};
</script>
